import React from 'react'
import styles from './shopping.module.css'

export default function shoppingType() {
  return (
    <div className={styles["shop-type"]}>
      <div className={styles["shop-type-top"]}>
        <div className={styles["shop-type-left"]}>
          <img src="" className={styles["shop-type-image"]} alt="" />
          <h3 className={styles["shop-type-title"]}></h3>
          <p className={styles["shop-type-description"]}></p>
        </div>
        <span className={styles["shop-type-mid"]}></span>
        <div className={styles["shop-type-right"]}>
          <img src="" className={styles["shop-type-image"]} alt="" />
          <h3 className={styles["shop-type-title"]}></h3>
          <p className={styles["shop-type-description"]}></p>
        </div>
      </div>
      <div className={styles["shop-type-bottom"]}>
        <a href='/shop'  className={styles['to-shop']}>
          <div className={styles['shop-left']}>
            <img src="" className={styles['shop-left-image']} alt="" />
            <span className={styles['shop-left-title']}></span>
          </div>
          <div className={styles['shop-right']}>
            <span className={styles['shop-left-desc']}>8815-88</span>
          </div>
        </a>
      </div>
    </div>
  )
}
